/* src/styles/RestaurantDetailPage.css */
.detail-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: #f9f9f9;
}

.detail-header {
  display: flex;
  align-items: center;
  padding: 16px;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0,0,0,0.1);
}

.back-btn {
  display: flex;
  align-items: center;
  gap: 4px;
  background: none;
  border: none;
  font-size: 14px;
  cursor: pointer;
  margin-right: 16px;
}

.detail-header h1 {
  font-size: 20px;
  margin: 0;
}

.detail-images {
  position: relative;
  background: #000;
}

.main-image {
  width: 100%;
  max-height: 300px;
  object-fit: cover;
}

.promo-overlay-detail {
  position: absolute;
  top: 12px;
  left: 12px;
  background: rgba(255,69,0,0.85);
  color: #fff;
  padding: 6px 10px;
  border-radius: 6px;
  font-weight: bold;
}

.thumbnails {
  display: flex;
  gap: 8px;
  padding: 8px 16px;
  overflow-x: auto;
}

.thumbnails img {
  height: 60px;
  width: 60px;
  object-fit: cover;
  border-radius: 6px;
  cursor: pointer;
}

.detail-info {
  flex: 1;
  padding: 16px;
}

.rating-distance {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
}

.rating, .distance {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
  color: #555;
}

.description, .menu-section {
  margin-bottom: 24px;
}

.description h2, .menu-section h2 {
  font-size: 18px;
  margin-bottom: 8px;
}

.menu-section ul {
  list-style: none;
  padding: 0;
}

.menu-section li {
  margin-bottom: 6px;
}

.menu-section strong {
  font-weight: 600;
}

@media (min-width: 768px) {
  .detail-info {
    padding: 24px 32px;
  }
  .main-image {
    max-height: 400px;
  }
}
