/* src/styles/PromosPage.css */

.promos-page {
  padding: 1.5rem;
  background-color: #fff;
}

.promos-page h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #ff6600;
}

.promos-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
}
